.promotion {
  position: relative;
}

.promotion > p {
  position: absolute;
  width: 100%;
  top: -5%;
  left: 50%;
  transform: translateX(-50%);
  font-family: inherit;
  font-size: clamp(20px, 1.5vw, 30px);
  font-variation-settings: "wght" 400, "wdth" 100;
  color: var(--back-main);
  text-align: center;

  @media (max-width: 992px) {
    font-size: 28px;
  }
}
